import axios from 'axios';
import React, {useState, useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Link} from 'react-router-dom'

function BreedDetail({match}) {
  const [breeds, setBreed] = useState([])

  useEffect(() => {
    axios({
      method: "GET",
      url: `http://127.0.0.1:8000/api/breed`
  }).then(response =>{
      setBreed(response.data)
      })
  },[])

  return (
   <div>
            <Link className='btn btn-outline-dark btn-lg' to={{pathname: `/create_books/`}}>Create</Link>
            <table className='table'>
                <thead>
                <tr className="table-warning">
                    <td scope="col">Book code</td>
                    <td scope="col">Name</td>
                    <td scope="col">Publishing</td>
                    <td scope="col">Author</td>
                    <td scope="col">Volume</td>
                    <td scope="col">Quantity</td>
                    <td scope="col">Update</td>
                    <td scope="col">Delete</td>

                </tr>
                </thead>
            <tbody>
                {breeds.map(breed => (
                    <tr>
                        <td>{breed.breed_name}</td>
                        <img>{breed.name}</img>
                        <td>{breed.breed_name}</td>

                    </tr>
                ))}
            </tbody>
            </table>
        </div>

  );
}

export default BreedDetail;