import './App.css';
import React, {useState, useEffect} from 'react';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import BreedDetail from "./components/Models/BreedDetail";
import VolunteerDetail from "./components/Models/VolunteerDetail";
import LoginPage from "./login";
import SingUp from "./registration";
import MainPage from "./MainPage";

function App() {
  return (
    <div className="App">
        <Router>
            <MainPage />
            <Switch>
                <Route path="/breeds" exact component={BreedDetail} />
                <Route path="/volunteer" exact component={VolunteerDetail} />
                <Route path="/login" exact component={LoginPage}/>
                <Route path="/register" exact component={SingUp}/>
            </Switch>
        </Router>
    </div>
  );
}

export default App;
